.videoCard{
    width:290px;
    margin-bottom:40px;

}
.videoCard_thumbnail{
    height:160px;
    width:280px;
}

.videoCard_info{
    display:flex;
    margin-top:10px;
    padding-right:30px;
}
.videoCard_text{
    margin-left:15px;
    cursor:pointer;
}
.videoCard_text > h4{
    font-size:14px;
    margin-bottom:5px;
}
.videoCard_text > p{
    font-size: 14px;
    color:gray;
}
.videoCard_avatar{
    height:30px !important;
    width:30px !important;
}
.durationOnImg{
    position:relative;
}
.duration{
    position:absolute;
    bottom:5%;
    right:10%;
    background:rgba(0, 0, 0, 0.76);
    color:white;
    padding:3px 5px;
    font-size:12px;
}