.searchPage{
    flex:0.8;
    padding:0 50px;
    margin-top:14vh;
    background-color: rgb(248, 244, 244);
}
.searchPage_filter{
    display:flex;
    align-items:center;
    color:#606060;
    font-size:xx-small !important;
    padding-top:10px;
}
.searchPage_filter >h2{
    margin-left:10px;
}
.searchPage >hr{
    height:1px;
    border:0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}

/* styles for channelRow */
.channelRow{
    margin-bottom:20px;
    display:flex;
    align-items:center;
    width:100%;
    justify-content: space-between;
}

.channelRow_text{
   
    display:flex;
    width:50%;
    flex-direction:column;
}
.channelRow_text>p{
    color:#606060;
    font-size:small !important;

}
.channelRow_logo_parent{
    width:300px;
    height:200px;
}
.channelRow_logo{
    height:120px !important;
    width:120px !important;
    margin:auto;
    margin-top:12%;
}
.channelRow_text>h4{
    display:flex;
    
    align-items:center;
    
}
.channelRow_text>h4>a{
    padding:5px;
}
.channelRow .subscribe_parent{
    width:20%;
}
.channelRow .subscribe{
    background:red;
    color:white;
    padding:5px 10px;
    outline:none;
    border:none;
    font-size:20px;
}
/* styles for videorow */
.videoRow{
    display:flex;
    margin-bottom:20px;
    width:100%;
    justify-content: space-between;
}
.videoRow >img{
    width:300px;
    height:200px;
}

.videoRow_text{
    display:flex;
    width:50%;
    flex-direction:column;
    align-items: flex-start;
}

.videoRow .empty{
    width:20%;
}
.videoRow_headline{
    font-size: 12px;
}
a{
    text-decoration: none;
    color:black;
}

@media only screen and (max-width:1000px){
    .searchPage{
        margin-left:20%;
        width:100%;
    }
    .videoRow>h3{
        font-size:10px;
    }
    .videoRow >img{
        width:250px;
        height:150px;
    }
    .channelRow_logo{
        margin-right:0;
        margin-left:70px;
    }
    .channelRow_text{
        margin-left: 90px;
        
    }
    .videoRow_text{
        margin-left:20px;
        width:70%;
       
    }
    .videoRow_text .description{
        display:none;
    }
    .empty{
        width:0%;
        display:none;
    }
}

@media only screen and (max-width:500px){
    .searchPage{
        flex:1;
        margin-left:0;
        width:100%;
    }
    .empty{
        display:none;
    }
    .videoRow{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}